<template>
  <el-container>
    <el-header class="cheader">
      <el-row>
        <el-col :span="6">
          <h2>打开钱包</h2>
        </el-col>
        <el-col :span="6"></el-col>
      </el-row>
    </el-header>

    <el-main class="cmain">
      <el-form :model="ruleForm" ref="ruleForm" :rules="rules" label-width="100px">
        
      

        <el-form-item label="钱包解锁密码" prop="password2">
          <el-input placeholder="请输入钱包解锁密码" v-model="ruleForm.password2" show-password></el-input>
        </el-form-item>
       
        <el-form-item>
          <el-button type="primary" @click="submitForm('ruleForm')">打开</el-button>
        </el-form-item>
        
      </el-form>
    </el-main>
  </el-container>
</template>
<style>
</style>

<script>
export default {
  data() {
    var validatePass = (rule, value, callback) => {
      if (value === "") {
        callback(new Error("请输入密码"));
      } else {
        callback();
      }
    };
    
    return {
      ruleForm: {
       
        password2: ""
      },
      rules: {
       
        password2: [{ validator: validatePass, trigger: "blur" }]
      }
    };
  },
  mounted() {
    
  },
  methods: {
    
    submitForm(formName) {
      let _this = this;
      this.$refs.ruleForm.validate(valid => {
        if (!valid) {
          return false;
        }

        this.$emit("setLoading", true, "解锁钱包");

        this.$axios({
          method: "post",
          data: JSON.stringify({
            id: new Date().getTime(),
            method: "wallet_unlockwallet",
            params: [this.ruleForm.password2]
          })
        }).then(response => {
          if (typeof response.data.error != "undefined") {
            this.$message({
              message: "错误，请稍后重试: " + response.data.error.message,
              type: "warning",
              duration: 3000,
              onClose: function() {
                _this.$emit("setLoading", false);
                _this.$router.go(0);
              }
            });
            return;
          }
          this.$message({
            message: "解锁成功!",
            type: "success",
            duration: 500,
            onClose: function() {
              _this.$emit("setLoading", false, "");
              _this.$emit("checkWalletStats");
            }
          });
        });
      });
    }
  }
};
</script>